body{
	background:radial-gradient(#e5e5e8,#dddd,#ffff);
}

.card{
	position:center;
	left:1.7rem;
	width:25rem;
}

.card:hover {
	box-shadow:5px 10px 20px 1px rgba(0,0,0,0.253)!important;
}

.card-body{
	padding:3rem 0 !important;
	padding-top: 1rem 0 !important;
	padding-left: 3rem 0 !important;
	padding-bottom: 2rem 0 !important;
}

.card-title{
	padding-top: 0rem;
}

.container-fluid .row{
	padding-top:1rem;
	left:10%;
	padding-bottom: 3rem;

}

.overflow{
	overflow:hidden;
}

.card-img-top{
	transform:scale(1);
	transition:transform 0.5s ease;
}

.card-img-top:hover{
	transform: scale(1.05);
}